* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: system-ui;
  background-color: aliceblue;
}

.displayPic {
  max-width: 200px;
  max-height: 200px;
  /* border: 1px solid rgba(0, 0, 0, 0.05); */
}

.itemList {
  display: grid;
  margin: 5rem auto;
  gap: 2rem;
  align-items: center;
  width: 70vw;
}

@media screen and (min-width: 600px) {
  .itemList {
    grid-template-columns: repeat(3, 1fr);
  }
}

.item {
  background-color: white;
  text-align: center;
  border-radius: 1rem;
  font-family: monospace;
  padding: 2rem;
  position: relative;
  /* parent must be set relative to have absolute chile #num */
}

.item h3 {
  margin: 1rem 0 0.5rem 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.index {
  position: absolute;
  top: 0;
  left: 0;
  /* padding: 1rem; */
  background-color: #000000;
  color: white;
  /* z-index: 0; */
  font-size: 1rem;
  /* padding: 1rem; */
  /* margin: 1rem; */
  border-top-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  padding: 0.5rem;
}

.header {
  text-align: center;
  margin-top: 4rem;
  text-transform: capitalize;
  font-size: 3rem;
}

.index:hover {
  background-color: #cd0606;
  transition: 0.3s ease-in-out;
}

.item:hover {
  /* translate: 5px -5px; */
  /* zoom: 1.1; */
  transform: scale(1.03);
  transition: 0.5s ease-in-out;
